body {
  margin: 0;
  font-family: Helvetica, sans-serif;
  background-color: #000000;
}

a {
  color: #ffffff;
}

/* header */

.header {
  padding: 15px;
  background-color: rgba(22, 22, 22, 0); /* Inicialmente transparente */
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  width: 100%;
  z-index: 3;
  transition: background-color 0.3s ease;
}

.header.scrolled {
  background-color: rgba(22, 22, 22, 0.8); /* Cor com transparência quando rolado */
}

